<!--顶部统计切换-->
<template>
  <div
    class="tabChange-style"
    v-loading="loadCount!==3"
    element-loading-background="rgba(0, 0, 0, 0.5)"
    element-loading-text="加载中"
  >
    <div class="swiper-container">
      <div class="swiper-wrapper">
        <div class="swiper-slide">
          <div class="title">业务处理满意率</div>
          <div class="bar-style">
            <bda-bar-charts
              v-if="ywclRes"
              :barType="barType"
              :chartsConfig="ywChartsConfig"
              :barWidth="2"
              :legendStatus="false"
              :yAxiosDw="dw"
            ></bda-bar-charts>
          </div>
        </div>
        <div class="swiper-slide">
          <div class="title">派单及时率</div>
          <div class="bar-style">
            <bda-bar-charts
              v-if="pdRes"
              :barType="barType"
              :chartsConfig="pdChartsConfig"
              :barWidth="2"
              :customizedStyle="customizedStyle"
              :legendStatus="false"
              :yAxiosDw="dw"
            ></bda-bar-charts>
          </div>
        </div>
        <div class="swiper-slide">
          <div class="title">回单及时率</div>
          <div class="bar-style">
            <bda-bar-charts
              v-if="hdRes"
              :barType="barType"
              :chartsConfig="hdChartsConfig"
              :barWidth="2"
              :legendStatus="false"
              :yAxiosDw="dw"
            ></bda-bar-charts>
          </div>
        </div>
      </div>
      <!--如果你需要前进后退按钮-->
      <div class="swiper-button-next"></div>
      <div class="swiper-button-prev"></div>
    </div>
  </div>
</template>

<script>
import Swiper from 'swiper';
import 'swiper/css/swiper.min.css';
import 'swiper/js/swiper.min.js';
import Interface from '../../../base/mixin/service-index-control';
import qs from 'qs';
import bdaBarCharts from '../../../widgets/bda-3d-barCharts/bda-3d-barCharts';


export default {
  name: "lbTabChange",
  mixins: [Interface],
  components: { bdaBarCharts },
  data() {
    return {
      customizedStyle: 0.74,//定制x轴的字体大小比例
      ywclRes: false,
      pdRes: false,
      hdRes: false,
      barType: 'stack',
      ywChartsConfig: {},
      pdChartsConfig: {},
      hdChartsConfig: {},
      loadCount: 0,
      dw: '%',
      tabActiveIndex: 1

    }
  },
  watch: {
    loadCount(v) {
      if (v === 3) {
        /* let $this = this;
         setTimeout(() => {

         }, 500)*/

      }
    },
    tabActiveIndex(v) {
      this.$parent.tabIndex = v;
    }
  },
  created() {
  },
  mounted() {
    this.initSwiper();
    //业务处理满意度
    this.getBusinessForSatisfaction(qs.stringify({
      dimensionality: this.$parent.dateType,
      timeStr: this.$parent.dateParams,
    })).then(res => {
      this.chartsDataFilter(res, '业务处理满意度');
    }).catch(() => {
      this.ywclRes = true;
      this.loadCount++;
    });
    this.getSendOrdersTimelinessRate(qs.stringify({
      dimensionality: this.$parent.dateType,
      timeStr: this.$parent.dateParams,
    })).then(res => {
      this.chartsDataFilter(res, '派单及时率');
    }).catch(() => {
      this.pdRes = true;
      this.loadCount++;
    });
    this.getAckOrdersTimelinessRateForType(qs.stringify({
      dimensionality: this.$parent.dateType,
      timeStr: this.$parent.dateParams,
    })).then(res => {
      let _data = {}
      if (res && res.length !== 0) {
        for (let item of res) {
          _data[item.busisubtype] = item.per
        }
      }
      this.chartsDataFilter(_data, '回单及时率');
    }).catch(() => {
      this.hdRes = true;
      this.loadCount++;
    });

  },
  methods: {
    initSwiper() {
      let $this = this;
      new Swiper('.swiper-container', {
        effect: 'coverflow',
        grabCursor: true,
        centeredSlides: true,
        slidesPerView: 'auto',
        //    前进后退按钮
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev',
        },
        //  loop: true,
        //   loopAdditionalSlides: 2,
        loopedSlides: 1,
        initialSlide: 1,
        coverflow: {
          rotate: 50,
          stretch: 0,
          depth: 100,
          modifier: 1,
          slideShadows: true
        },
        on: {
          slideChange: function () {
            $this.tabActiveIndex = this.activeIndex;
          },
        },
      });

    },
    chartsDataFilter(data, name) {
      let dataMap = {
        dimension: [],
        quotaMap: {}
      };
      dataMap.quotaMap[name] = [];
      if (data) {
        for (let key in data) {
          dataMap.dimension.push(key);
          dataMap.quotaMap[name].push(data[key])
        }
      }
      let colors = ['#0083fb', '#006fd4']
      if (name === '业务处理满意度') {
        dataMap.colorMap = { '业务处理满意度': colors };
        this.ywChartsConfig = dataMap;
        this.ywclRes = false;
        this.$nextTick(() => {
          this.ywclRes = true;
        })
      }
      if (name === '回单及时率') {
        dataMap.colorMap = { '回单及时率': colors };
        this.hdChartsConfig = dataMap;
        this.hdRes = false;
        this.$nextTick(() => {
          this.hdRes = true;
        })
      }
      if (name === '派单及时率') {
        dataMap.colorMap = { '派单及时率': colors };
        this.pdChartsConfig = dataMap;
        this.pdRes = false;
        this.$nextTick(() => {
          this.pdRes = true;
        })
      }
      this.loadCount++;
    }
  }
}
</script>

<style scoped lang="scss">
.tabChange-style {
  width: 90%;
  height: calc(100% - 30px);
  position: relative;
  padding: 15px 0px;
  margin: auto;

  .swiper-container {
    width: 100%;
    height: 100%;
    position: relative;

    .swiper-slide {
      width: 60%;
      height: calc(100% - 2px);
      position: relative;
      border: 1px solid #20cff1;
      border-radius: 5px;
      box-shadow: #20cff1 0px 0px 5px inset;

      .title {
        line-height: 35px;
        text-align: center;
        color: #fff;
        font-size: 16px;
      }

      .bar-style {
        width: 100%;
        height: calc(100% - 38px);
        position: relative;
      }
    }

    .swiper-slide-prev {
      transform: translate3d(0px, 0px, -500px) rotateX(0deg) rotateY(-60deg) !important;
    }

    .swiper-slide-next {
      transform: translate3d(0px, 0px, -500px) rotateX(0deg) rotateY(60deg) !important;
    }
    .swiper-button-next,
    .swiper-button-prev {
      top: 20%;
      width: 10%;
      height: 80%;
      color: transparent;
    }
  }
}
</style>